/*Global style
----------------------------*/
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: #343434;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  line-height: 1.5;
}
img {
  width: 300px;
}
a {
  color: #ffe66d;
}
a:hover {
  text-decoration: none;
}
h1 {
  font-size: 100px;
  line-height: 1;
}
h2 {
  font-size: 45px;
}
h1,
h2 {
  font-family: "Caveat", cursive;
  font-weight: 400;
  margin: 0;
}
h3 {
  margin-bottom: 0;
}
.content-wrap {
  max-width: 900px;
  width: 85%;
  margin: 0 auto;
  padding: 60px 0px;
}
.item-details h3 + p {
  font-style: italic;
}
.item-details h3 ~ p {
  margin: 0;
}
.divider > section {
  border-bottom: 1px dashed #343434;
  padding: 25px 0;
}

.divider > section:last-of-type {
  border-bottom: none;
}

/*Profile
----------------------------*/
header {
  background-color: #2f3061;
  color: #f7fff7;
}

/*Project 
----------------------------*/
.project {
  background: #f7fff7;
}
.project a {
  color: #f7fff7;
  background-color: #2f3061;
  text-decoration: none;
  padding: 8px;
  border-radius: 8px;
  display: inline-block;
}
.project a:hover {
  background: rgba(47, 48, 97, 80%);
}
.project-item {
  overflow: hidden;
}

/*Work Experience 
----------------------------*/
.work {
  background-color: #a1c6d7;
}

/* Education  
----------------------------*/
.education {
  background-image: url("img/edu.jpg");
  background-size: cover;
  background-position: top right;
  padding-bottom: 100px;
}

.education p {
  width: 60%;
}

/* Contact Info
----------------------------*/
footer {
  background-color: #343434;
  color: #f7fff7;
}

#project-video {
  margin: 400px auto 0px;
}
.contact {
  list-style-type: none;
  padding: 0px;
}
.contact a {
  padding: 15px;
  display: inline-block;
}

/* Responsive
----------------------------*/

@media screen and (min-width: 750px) {
  header,
  footer {
    text-align: center;
  }
  .project-item img {
    float: left;
    margin-right: 20px;
  }

  .job-item {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 20px;
  }

  .contact {
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 749px) {
  h1 {
    font-size: 75px;
    line-height: 0.9;
    margin-bottom: 20px;
  }

  h2 {
    line-height: 1;
  }

  .contact a {
    padding: 5px;
  }
}
